  padding: 20px 0;
}
.accordion-price__element:before {
  content: "";
  height: 1px;
  background-color: #3f82ff;
  position: absolute;
  left: 0;
  top: -1px;
  width: 0;
  transition: all 0.3s;
}
.accordion-price__element.is-active .plus span:last-child {
  transform: rotate(0);
}
