  font-weight: 700;
  font-size: 20px;
  line-height: 140%;
  color: #000;
  padding-left: 38px;
  position: relative;
  transition: all 0.3s;
}
.accordion-price__button:after {
  content: url("../../img/sprite/arrow-price.svg");
  width: 18px;
  height: 18px;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
  display: flex;
  align-items: center;
}

.plus {
  width: 30px;
  height: 30px;
  cursor: pointer;
  position: relative;
}
.plus span {
  transition: all 0.2s;
  position: absolute;
  display: block;
  height: 2px;
  background-color: #3F82FF;
  width: 100%;
  top: calc(50% - 1px);
  left: 0;
}
.plus span:nth-child(2) {
  width: 8px;
  height: 8px;
  border-radius: 100px;
  left: 50%;
  transform: translate(-4px, -3px);
}
.plus span:last-child {
  transform: rotate(-90deg);
}

